<template>
    <div class="hheader">
        <van-nav-bar :title="header.title" @click-left="onClickLeft">
            <i class="iconfont icon-zuojiantou" v-if="header.back=='true'" slot="left"></i>
            <div v-else slot="left">
                <slot name="hleft"></slot>
            </div>
            <div slot="right">
                 <slot name="hright"></slot>
            </div>
        </van-nav-bar>
    </div>
</template>

<script>
export default {
    props: {
        header: {
            title: "",
            back: {
                default: "false"
            },
            backUrl: ""
        }
    },
    methods: {
        //点击左侧
        onClickLeft() {
            console.log(this.header);
            if (this.header.backUrl) {
                //指定跳转页面
            } else if (this.header.back == 'true') {
                //后退页面
                this.$router.back();
            } else {
                //不跳转页面
                //this.$store.commit("showLeftMenu");
                this.$router.push('/my');
            }
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.hheader {
    background-color: $color;
    color: white;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    .van-hairline--bottom.van-nav-bar {
        background-color: $color;
    }
    .van-nav-bar__left {
        .iconfont {
            font-size: rem(60);
        }
    }
}
</style>



