<template>
    <div class="footer">
        <van-tabbar v-model="btnactive">
            <van-tabbar-item @click.native="editBtn(0)">
                <i class="iconfont icon-mubiao" slot="icon"></i>
                <p>首页</p>
            </van-tabbar-item>
            <van-tabbar-item @click.native="editBtn(1)">
                <i class="iconfont icon-browse" slot="icon"></i>
                <p>监控</p>
            </van-tabbar-item>
            <van-tabbar-item @click.native="editBtn(2)">
                <i class="iconfont icon-dian" slot="icon"></i>
                <p>报警</p>
            </van-tabbar-item>
            <van-tabbar-item @click.native="editBtn(3)">
                <i class="iconfont icon-xiangji" slot="icon"></i>
                <p>相册</p>
            </van-tabbar-item>
        </van-tabbar>
    </div>
</template>

<script>
export default {
    data() {
        return {
            active: 0
        };
    },
    computed: {
        btnactive: {
            get: function() {
                return this.$store.state.btnactive;
            },
            set: function() {}
        }
    },
    methods: {
        editBtn(v) {
            let self=this;
            //获取地址栏参数
            let project_id=HGetHashUrl("projectId");
            switch (v) {
                case 0:
                    self.$router.push("/home");
                    break;
                case 1:
                    self.$router.push("/monitoring?projectId="+project_id);
                    break;
                case 2:
                    self.$router.push("/waring?projectId="+project_id);
                    break;
                case 3:
                    self.$router.push("/photo?projectId="+project_id);
                    break;
            }
            this.$store.commit("editFooter", v);
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
</style>



