// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
//路由
import router from './router'
//状态管理
import store from './vuex/index'
//加载数据
import hpost from './api/http';
Vue.prototype.$ = hpost;

//加载组件
import { NavBar, PullRefresh, List, Popup, Button, Swipe, SwipeItem, Row, Col, Tabbar, TabbarItem, Field, Checkbox, Picker } from 'vant';
Vue.use(NavBar);
Vue.use(PullRefresh);
Vue.use(List);
Vue.use(Popup);
Vue.use(Button);
Vue.use(Swipe).use(SwipeItem);
Vue.use(Tabbar).use(TabbarItem);
Vue.use(Row).use(Col);
Vue.use(Field).use(Checkbox);
Vue.use(Picker);
import axios from 'axios'

// 网络请求配置
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.headers.get['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.withCredentials = true;
// 添加一个请求拦截器
axios.interceptors.request.use(
  config => {
    //if (config.data.isShowLoading) Indicator.open();
    //delete config.data.isShowLoading;  //删除属性
    config.headers = { 'Content-Type': 'application/json;charset=UTF-8' };
    // console.log("请求前拦截:");
    // console.log(config);
    return config
  },
  err => {
    return Promise.reject(err)
  }
);
/*加载自定义公用组件*/
import hheader from './components/hheader.vue';
Vue.component("hheader", hheader)
import hfooter from './components/footer.vue';
Vue.component("hfooter", hfooter)


Vue.config.productionTip = false;
/*过滤器*/
Vue.filter("workStatus", re => {
  switch (re) {
    case "1":
      return "正常";
      break;
    case "0":
      return "报警";
      break;
  }
});
Vue.filter("dealStatus", re => {
  switch (re) {
    case "1":
      return "已处理";
      break;
    case "0":
      return "未处理";
      break;
  }
});
Vue.filter("deviceType", re => {
  switch (re) {
    case "1":
      return "DTU";
      break;
    case "2":
      return "传感器";
      break;
  }
});
Vue.filter("degree", re => {
  switch (re) {
    case "1":
      return "一级";
      break;
    case "2":
      return "二级";
      break;
    case "3":
      return "三级";
      break;
  }
});
Vue.filter("deviceParam", re => {
  switch (re) {
    case "shid":
      return "湿度";
      break;
    case "ylz":
      return "压力";
      break;
    case "nbz":
      return "南北振";
      break;
    case "spwy":
      return "水平位移";
      break;
    case "bcwy":
      return "本次位移";
      break;
    case "wd":
      return "温度";
      break;
    case "fs":
      return "风速";
      break;
    case "sd":
      return "深度";
      break;
    case "ljwy":
      return "累计位移";
      break;
    case "fx":
      return "风向";
      break;
    case "jgcj":
      return "激光测距";
      break;
    case "ljcj":
      return "累计沉降";
      break;
    case "qxd":
      return "倾斜度";
      break;
    case "jzgd":
      return "建筑高度";
      break;
    case "temp":
      return "温度";
      break;
    case "ljyl":
      return "累计雨量";
      break;
    case "dxz":
      return "东西振";
      break;
    case "mszl":
      return "锚索张力";
      break;
    case "jdwy":
      return "绝对位移";
      break;
    case "sxwy":
      return "竖向位移";
      break;
    case "x":
      return "x轴";
      break;
    case "addup":
      return "累计沉降值";
      break;
    case "y":
      return "y轴";
      break;
    case "z":
      return "z轴";
      break;
    case "tq":
      return "天气（1：雪；2：雨；3：阴；4：晴）";
      break;
  }
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



