import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld';
import store from '../vuex/index'

Vue.use(Router)
const routes = [
  {
    path: '/',
    name: 'program',
    component: resolve => require(['../views/program.vue'], resolve),
  },
  {
    path: '/program',
    name: 'program',//基坑监测项目
    component: resolve => require(['../views/program.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/waring',
    name: 'waring',//数据报警
    component: resolve => require(['../views/waring.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/monitoring',
    name: 'monitoring',//监控界面
    component: resolve => require(['../views/monitoring.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/stationDetial',
    name: 'stationDetial',//站点详情
    component: resolve => require(['../views/stationDetial.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/my',
    name: 'my',//个人中心
    component: resolve => require(['../views/my.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/photo',
    name: 'photo',//站点图片
    component: resolve => require(['../views/photo1.vue'], resolve),
    meta: { requireAuth: true }
  },
  {
    path: '/login',
    name: 'login',//登录
    component: resolve => require(['../views/login.vue'], resolve),
    meta: { requireAuth: false }
  },
  {
    path: '/home',
    name: 'home',//主页
    component: resolve => require(['../views/home.vue'], resolve),
    meta: { requireAuth: true }
  }, {
    path: '/test',
    name: 'test',//测试
    component: resolve => require(['../views/test.vue'], resolve),
    meta: { requireAuth: true }
  },
  , {
    path: '/loading',
    name: 'loading',//引导页
    component: resolve => require(['../views/loading.vue'], resolve),
    meta: { requireAuth: false }
  },
  {
    path: '/forgetPwd',
    name: 'forgetPwd',//忘记密码
    component: resolve => require(['../views/forgetPwd.vue'], resolve),
    meta: { requireAuth: false }
  },
  {
    path: '/detailList',
    name: 'detailList',//更多信息
    component: resolve => require(['../components/detailList.vue'], resolve),
    meta: { requireAuth: false }
  },
]
// 页面刷新时，重新赋值token
if (window.localStorage.getItem('tokenId')) {
  store.state.tokenId = window.localStorage.getItem('tokenId');
  store.state.userId = window.localStorage.getItem('userId');
}

const router = new Router({
  hashbang: true,
  history: true,
  transitionOnLoad: true,
  routes
});
//拦截
router.beforeEach((to, from, next) => {
  //  next();
  //  return;
  //判断是否需要登录
  if (to.matched.some(record => record.meta.requireAuth)) {
    //判断Token
    if (store.state.token) {
      //需要引入store
      next();
    } else {
      //Token失效
      if (!store.state.tokenId) {
        next({
          path: '/login'
        });
      } else {
        next();
      }
    }
  } else {
    next();
  }
});
export default router;




