// 更改 Vuex 的 store 中的状态的唯一方法是提交 mutation
// this.$store.commit(method, params)

import store from './'

export default {
  updateLoading(state, status) {
    // clearTimeout(state.loadingTimer);
    // state.loadingTimer = setTimeout(() => {
    state.isLoading = status
    // }, 10)
  },
  hincrement(state, status) {
    // 变更状态
    state.hconfirm = status
  },
  updatePaySection(state, status) {
    console.log("<<>>" + status);
    state.paySection = status
  },
  //退出系统
  hexitSystem(state) {
    state.tokenId = "";
    state.userId = "";
    state.userName = "";
    state.userImg = "";
    localStorage.removeItem("tokenId");
    localStorage.removeItem("userId");
  },
  //显示左侧菜单
  showLeftMenu(state) {
    state.show = !state.show;
  },
  //显示左侧菜单
  editFooter(state, index) {
    state.btnactive = index;
  }
  ,//登录
  loginStystem(state, obj) {
    console.log(">>>>>loginStystem");
    state.tokenId =obj.tokenId;
    state.userId =obj.userId;
    state.userName = "";
    state.userImg = "";
    localStorage.setItem("tokenId",obj.tokenId);
    localStorage.setItem("userId",obj.userId);
  }
}




