//序列化参数
function setUrl(obj) {
    var txt = "";
    for (var v in obj) {
        if (txt == "") {
            txt = "?" + v + "=" + obj[v];
        } else {
            txt += "&" + v + "=" + obj[v];
        }
    }
    return txt;
}
// 底部toast提示
function MToast(msg) {
    if (!msg) msg = "";
    return "";
}
