// 显示加载中进度条
export function showLoading ({state, commit}) {
    commit('updateLoading', true)
  }
  
  // 隐藏加载中进度条
  export function hideLoading ({state, commit}) {
    commit('updateLoading', false)
  }
  
  // 隐藏加载中进度条
  export function hconfirme ({state, commit},status) {
    commit('hincrement', status)
  }
  
  // 隐藏加载中进度条
  export function hexitSystem ({state, commit}) {
    commit('hexitSystem')
  }
  // 修改支付方式
  export function editPaySection ({state, commit}, status) {
    commit('updatePaySection', status)
  }
  
  
  
  


