<template>
  <div id="app">
    <router-view/>
    <van-popup v-model="show" position="left" :overlay="true" :close-on-click-overlay="true" class="hv-popup">
      <div class="left-menu">
        <div class="left-menu-back" @click="closePopup"></div>
        <div class="left-menu-main">
          <myMenu></myMenu>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import myMenu from './views/leftMenu.vue';
export default {
    name: "App",
    data() {
        return {
            //show: true
        };
    },
    computed: {
        show: {
            get: function() {
                return this.$store.state.show;
            },
            set: function() {}
        }
    },
    methods: {
        //关闭
        closePopup() {
            this.$store.commit("showLeftMenu");
        }
    },components:{
      myMenu
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "./assets/hcsy.scss";
.hv-popup,
.left-menu {
    position: fixed;
    width: 100%;
    height: rem(2208);
    z-index: 10000000;
    font-size: rem(40);
}
.left-menu-back {
    background-color: #000;
    position: absolute;
    opacity: 0.5;
    width: 100%;
    height: rem(2208);
}
.left-menu-main {
    background-color: white;
    position: absolute;
    width: 80%;
    height: rem(2208);
    z-index: 100;
}
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f7f7f7;
}
</style>



